@import url(../stylesheets/hljs_styles/darkula.css);/*!
样式来自http://www.cnblogs.com/zhangjk1993/p/5442676.html
*/.markdown{color:#2f2f2f;font-size:16px}.markdown h1,.markdown h2,.markdown h3,.markdown h4,.markdown h5,.markdown h6{color:#555555;font-weight:bold;line-height:1.8}.markdown h1{font-size:36px}.markdown h2{font-size:24px}.markdown h3{font-size:22px}.markdown h4{font-size:20px}.markdown h5{font-size:18px}.markdown h6{font-size:16px}.markdown p,.markdown a,.markdown ul,.markdown ol{font-size:16px;font-weight:normal;line-height:1.7;margin-bottom:25px;word-break:break-all}.markdown a{color:#4094c7;text-decoration:none}.markdown a:hover{color:#075b8d}.markdown ul,.markdown ol{padding-left:24px}.markdown code,.markdown pre{border-radius:3px;background-color:#fdf6e3;color:#657b83;font-size:14px;font-family:Consolas, Monaco, Andale Mono, monospace}.markdown code{padding:2px 4px}.markdown pre{line-height:20px;overflow:auto;padding:10px;border:1px solid rgba(0,0,0,0.15)}.markdown pre code{border:0;display:inline;max-width:initial;padding:0;margin:0;overflow:initial;line-height:inherit;font-size:.85em;white-space:pre;background:0 0}.markdown blockquote{background-color:whitesmoke;border-left:4px solid #999999;padding:10px 15px;margin-left:0;margin-bottom:20px;line-height:30px}.markdown blockquote p{margin:0;font-size:15px;line-height:1.7}.markdown hr{text-align:left;color:#999;height:2px;padding:0;margin:16px 0;background-color:#e7e7e7;border:0 none}.markdown table{width:100%;margin-bottom:20px;border:1px solid #dddddd;border-collapse:collapse;border-left:none}.markdown table th{padding:8px;border-left:1px solid #dddddd;border-top:1px solid #dddddd;line-height:20px;font-weight:bold !important}.markdown table td{padding:8px;border-left:1px solid #dddddd;border-top:1px solid #dddddd;line-height:20px;vertical-align:top}.markdown img{max-width:100%}.flex,.flex-column,.flex-center-justify,.flex-center,.flex-center-align{display:-webkit-box;display:-ms-flexbox;display:flex}.flex-column{-webkit-box-orient:vertical;-webkit-box-direction:normal;-ms-flex-direction:column;flex-direction:column}.flex-center-justify,.flex-center{-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center}.flex-center-align,.flex-center{-webkit-box-align:center;-ms-flex-align:center;-ms-grid-row-align:center;align-items:center}.article{background-color:#fff}.article .head-img{width:100%;height:500px;background-repeat:no-repeat;background-size:cover}@media (max-width: 650px){.article .head-img{height:70vw}}@media (max-width: 817px){.article .header,.article .markdown,.article .duoshuo{margin:0 20px}}.article .header{color:#555555;border-bottom:1px solid #efeaea}.article .header .title{margin-bottom:0;font-size:36px}



